.side-bar {
  background-color: #fff;
  position: absolute;
  top: -155%;
  left: 0;
  width: 100%;
  transition: 500ms ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  border: 1px solid #f1f1f1;
  border-top: 3px solid #ff0f21;
  z-index: 555;
  margin-bottom: 1rem;
  padding-inline: calc((100vw - 82%) / 2);

  @media screen and (min-width: 924px) {
    display: none;
  }

  .NavLink {
    font-size: 0.75rem;
    color: #101010;
    font-weight: 700;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 1em 1em;
    margin: 1em 2em;
    width: 100%;
    cursor: pointer;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #f1f1f1;
    transition: 0.7s;

    &:hover {
      opacity: 0.7;
      background-color: rgba(0, 0, 0, 0.03);
    }

    &:last-child {
      border: none;
    }
  }

  .shipment {
    display: flex;
    justify-content: flex-start;

    :last-child {
      font-size: 1.4rem;
      color: #ff0f21;
    }
  }
}

.animation {
  top: 5.38rem;
}
